<template>
  <div class="table_holder">
    <div v-if="food.length > 0">
      <table>
        <thead>
          <th id="name">Název</th>
          <th id="count">Počet</th>
          <th id="prot">Bílkoviny</th>
          <th id="carb">Sacharidy</th>
          <th id="fats">Tuky</th>
          <th id="ener">Energie</th>
          <th id="action"></th>
        </thead>
        <tbody>
          <tr v-for="(f, i) in food" :key="i">
            <td>{{ f.name }}</td>
            <td class="amount">
              <span
                ><i class="fas fa-minus" @click="down(f.id)"></i>
                {{ f.amount }}
                <i class="fas fa-plus" @click="up(f.id)"></i
              ></span>
            </td>
            <td>{{ f.proteins }}g</td>
            <td>{{ f.carbs }}g</td>
            <td>{{ f.fats }}g</td>
            <td>{{ f.energy }}kcal</td>
            <td><i class="fas fa-trash red" @click="remove(f.id)"></i></td>
          </tr>
          <tr class="sum">
            <td>Celkem</td>
            <td>{{ sum.amount }}</td>
            <td>{{ sum.proteins }}g</td>
            <td>{{ sum.carbs }}g</td>
            <td>{{ sum.fats }}g</td>
            <td>{{ sum.energy }}kcal</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-if="food.length > 0" class="recomendation">
      <p>
        Protože se požadavky na výživu mění v závislosti na věku, hmotnosti,
        výšce, pohlaví a úrovni fyzické aktivity, nepředstavují tato denní
        množství cíle, jichý má být dosaženo, ale doporučené hodnoty, které byly
        stanoveny pro zdravého člověka s průměrnou hmotností a průměrnou
        fyzickou aktivitou.
      </p>
      <p>Požadavky na energii se pohybují v rozmezí:</p>
      <p>1 500 až 2 000 kcal (6 300 až 8 400 kJ) denně u dětí</p>
      <p>2 200 až 2 700 kcal (9 240 až 11 340 kJ) denně u mužů</p>
      <p>1 800 až 2 200 kcal (7 560 až 9 240 kJ) denně u žen</p>
    </div>
    <div class="nothing" v-else>
      <p>Vyberte si jídlo z nabídky</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    food: {
      type: Array,
      required: true,
    },
  },
  methods: {
    up(id) {
      this.food.find((f) => f.id === id).amount++;
    },
    down(id) {
      if (this.food.find((f) => f.id === id).amount > 1) {
        this.food.find((f) => f.id === id).amount--;
      } else {
        this.$emit("remove", id);
      }
    },
    remove(id) {
      this.$emit("remove", id);
    },
  },
  computed: {
    sum() {
      return this.food.reduce(
        (acc, cur) => {
          acc.proteins += cur.proteins * cur.amount;
          acc.carbs += cur.carbs * cur.amount;
          acc.fats += cur.fats * cur.amount;
          acc.energy += cur.energy * cur.amount;
          acc.amount += cur.amount;
          return acc;
        },
        { proteins: 0, carbs: 0, fats: 0, energy: 0, amount: 0 }
      );
    },
  },
};
</script>

